@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
#home{color:#3f3d56}#home .bigWave-container{position:relative;overflow:hidden}@media only all and (max-width: 2100px){#home .bigWave-container{height:320px}}@media only all and (max-width: 1400px){#home .bigWave-container{height:290px}}#home .bigWave-container .bigWave{position:absolute;right:-40px;top:-140px}#home .content{display:flex}#home .content .figure-container{padding-left:88px;padding-top:30px}@media only all and (max-width: 2100px){#home .content .figure-container .figure{width:100%}}@media only all and (max-width: 1400px){#home .content .figure-container .figure{width:70%}}#home .content .intro{padding-left:100px;padding-top:50px}#home .content .intro .intro-title{font-weight:700;font-size:36px;line-height:44px}#home .content .intro .intro-text{font-weight:500;font-size:26px;line-height:32px;margin:30px 0px}#home .content .intro .join{background:linear-gradient(269.8deg, #e6b486 -20.66%, #e62525 113.09%);border-radius:36px;font-size:26px;color:#fff;padding:12px 40px;font-weight:500}#home .content .intro .join:hover{background:linear-gradient(269.8deg, #e62525 -20.66%, #e6b486 113.09%)}

.infoWindow{display:flex}.infoWindow .text-info{margin-right:40px}.infoWindow .text-info p{font-weight:bold;font-size:16px}.infoWindow .pic-info img{width:150px;height:150px}.infoWindow .pick-btn{font-weight:bold;font-size:16px;color:#fff;background:rgba(218,63,53,0.81);border-radius:5px;padding:6px 10px}

#map-content{display:flex;padding-left:40px;padding-top:5px}#map-content .mode{font-weight:bolder}#map-content .show-container{margin-left:20px;margin-top:10px}#map-content .show-container .user-panel{margin-top:20px;width:350px;height:400px;border:#e6b486 3px solid;border-radius:10px;overflow:auto}#map-content .show-container .user-panel p{font-size:18px;color:#3f3d56;text-align:center;font-weight:bold;margin:0}#map-content .show-container .user-panel .drop{color:#e62525;font-weight:bolder}#map-content .show-container .user-panel .pick{color:#e6b486;font-weight:bolder}#map-content .show-container .font_I{font-style:normal;font-weight:bold;font-size:72px;line-height:88px;color:#e6b486;margin-left:10px;margin-right:10px}#map-content .show-container .font_C{font-style:normal;font-weight:bold;font-size:72px;line-height:88px;color:#e6b486}#map-content .show-container .font_an{color:#e6b486;font-weight:bold;font-size:36px;line-height:44px;margin-right:10px}#map-content .show-container button{font-weight:bold;font-size:36px;color:#fff;background:rgba(218,63,53,0.81);border-radius:30px;padding:6px 40px;transform:translateY(-10px)}

#upload-form label{background:linear-gradient(269.8deg, #e6b486 -20.66%, #e62525 113.09%);border-radius:5px;font-size:16px;color:#fff;padding:12px;font-weight:500;cursor:pointer}#upload-form label input{display:none}#upload-form .output{margin-top:20px}#upload-form .output .Skeleton{width:300px;height:300px}#upload-form .output img{width:300px;height:300px}

#register{padding-left:100px;padding-top:30px}#register .back-container{margin-bottom:20px}#register .back-container .back{background:linear-gradient(269.8deg, #e6b486 -20.66%, #e62525 113.09%);border-radius:5px;font-size:16px;color:#fff;padding:12px;font-weight:500;cursor:pointer}#register .main{display:flex}#register .main .form-content{width:350px}#register .main .picture-content{margin-left:200px}

#Map{padding-top:160px}#Map .smallWave-container{position:absolute;right:0;top:0}

#main{position:relative;min-width:1090px;height:100vh}@media only all and (max-width: 2100px){#main{margin-left:250px}}@media only all and (max-width: 1400px){#main{margin-left:200px}}

#sidebar{background:linear-gradient(181.66deg, #e6b486 .67%, #e62525 104.61%);height:100vh;display:flex;align-items:center;justify-content:center;z-index:10}@media only all and (max-width: 2100px){#sidebar{width:250px}}@media only all and (max-width: 1400px){#sidebar{width:200px}}#sidebar a{display:block;text-decoration:none;text-align:center;font-weight:700;color:#fff}@media only all and (max-width: 2100px){#sidebar a{font-size:36px;line-height:93px;height:93px}}@media only all and (max-width: 1400px){#sidebar a{font-size:28px;line-height:63px;height:63px}}#sidebar .selected{background:#da3f35;border-radius:10px;width:100%}@media only all and (max-width: 2100px){#sidebar .selected{height:93px}}@media only all and (max-width: 1400px){#sidebar .selected{height:63px}}#sidebar .title{position:absolute;border:5px solid #e6b486;box-sizing:border-box;border-radius:50px;z-index:10}@media only all and (max-width: 2100px){#sidebar .title{width:266px;height:103px;left:114px;top:80px}}@media only all and (max-width: 1400px){#sidebar .title{width:180px;height:63px;left:110px;top:60px}}#sidebar .title .title-left{position:absolute;font-weight:700;color:#fff;z-index:10}@media only all and (max-width: 2100px){#sidebar .title .title-left{width:87px;height:44px;left:29px;top:24px;font-size:36px}}@media only all and (max-width: 1400px){#sidebar .title .title-left{width:87px;height:44px;left:18px;top:8px;font-size:24px}}#sidebar .title .title-right{position:absolute;font-weight:700;color:#3f3d56;z-index:10}@media only all and (max-width: 2100px){#sidebar .title .title-right{width:108px;height:44px;left:139px;top:24px;font-size:36px}}@media only all and (max-width: 1400px){#sidebar .title .title-right{width:108px;height:44px;left:95px;top:8px;font-size:24px}}#sidebar .navbar{width:100%}

*{padding:0;margin:0;box-sizing:border-box;font-family:"Montserrat",sans-serif}.btn{border:none;outline:none;cursor:pointer}.form-btn{font-weight:bold;font-size:16px;color:#fff;background:rgba(218,63,53,0.81);border-radius:5px;padding:6px 10px}

